import axios from 'axios';
import { BASE_URI } from '../config/index';

const requestXhrHttp = axios.create({
  baseURL: BASE_URI,
  timeout: 7000,
  headers: {
    'Content-Type': 'application/json;charset=UTF-8',
    Authorization: localStorage.getItem('token') || '',
  },
});

// 添加请求拦截器
requestXhrHttp.interceptors.request.use(
  (config) => {
    return config;
  },
  function (error) {
    // 对请求错误做些什么
    return Promise.reject(error);
  }
);

// 添加响应拦截器
requestXhrHttp.interceptors.response.use(
  function (response) {
    // 对响应数据做点什么
    return response.data;
  },
  function (error) {
    return Promise.reject(error);
  }
);

export default requestXhrHttp;
