import React, { useEffect, useRef, useState } from 'react';
import { ImageUploader, Toast, Dialog, Button, Form, Input, TextArea, Picker, DatePicker } from 'antd-mobile';
import { ImageUploadItem } from 'antd-mobile/es/components/image-uploader';
import Dayjs from 'dayjs';
import { uploadSingleFile } from '../../services';
import { createActivity, queryClass } from './services';
import { queryClassType } from './data';
export type ActivityType = {
  title: string; // 活动标题
  introduce: string; // 活动介绍
  grade: string; // 活动分数
  startTime: string; // 活动开始时间
  endTime: string; // 活动结束时间
  location: string; // 活动地点
  organization: string; // 活动组织
  initiator: string; // 活动发起人姓名
  rankName: string; // 活动级别
  rankId: string; // 分院编号或者是班级编号
  canNumber: string; // 可以参与人数
  signWay: string; // 活动签到方式
  stStatus: string; // 活动状态
};
export default function ApplyActivity() {
  const [fileList, setFileList] = useState<ImageUploadItem[]>([]);
  const [datePickerVis, setDatePickerVis] = useState(false);
  const [signWayVis, setSignWayVis] = useState(false);
  const [rankNameVis, setRankNameVis] = useState(false);
  const [rankIdVis, setRankIdVis] = useState(false);
  const queryClassList = useRef<queryClassType[]>();
  const studentStatus = JSON.parse(window.localStorage.getItem('studentStatus') ?? '');
  const fileBol = useRef<File>();
  async function mockUpload(file: File) {
    fileBol.current = file;
    return {
      url: URL.createObjectURL(file),
      type: file.type,
      name: file.name,
    };
  }
  useEffect(() => {
    queryClass({
      pageNumber: 1,
      pageSize: 999,
    }).then((res) => {
      queryClassList.current = res.data.list;
      console.log(queryClassList.current);
    });

    // 初始化数据
    from.setFieldsValue({
      organization: studentStatus.collegeName,
      initiator: studentStatus.name,
    });
  }, []);
  const [from] = Form.useForm();
  const selectItem = useRef('');
  function onFinish(values: ActivityType) {
    if (fileBol.current) {
      let formData = new FormData();
      formData.append('file', fileBol.current);
      formData.append('fileType', 'sutuo');
      uploadSingleFile(formData)
        .then((res) => {
          const uri = res.data.uri;
          console.log('res', uri);
          const rankId = queryClassList.current?.find((item) => (item.className = values.rankId))?.collegeId;
          const sendData = {
            ...values,
            grade: values.grade ? Number.parseFloat(values.grade) : 0.0,
            canNumber: values.canNumber ? Number.parseInt(values.canNumber) : 0,
            imgUrl: uri,
            rankId: rankId,
          };
          console.log(sendData, 'sendDatasendData');

          createActivity(sendData).then((res) => {
            console.log(res);
            Toast.show({
              icon: 'success',
              content: '申请活动成功',
            });
          });
        })
        .catch(() => {
          Toast.show({
            icon: 'fail',
            content: '申请活动失败',
          });
        });
    }
  }

  return (
    <div>
      <Form
        name="form"
        form={from}
        onFinish={onFinish}
        footer={
          <Button
            block
            type="submit"
            color="primary"
            size="large"
            onClick={() => {
              console.log('sad', fileList);
            }}
          >
            提交
          </Button>
        }
      >
        <Form.Item name="title" label="标题">
          <Input placeholder="请输入活动标题" />
        </Form.Item>
        <Form.Item name="imgUrl" label="活动图片">
          <ImageUploader
            value={fileList}
            onChange={setFileList}
            upload={mockUpload}
            maxCount={1}
            onDelete={() => {
              return Dialog.confirm({
                content: '是否确认删除',
              });
            }}
          />
        </Form.Item>
        <Form.Item name="introduce" label="活动内容">
          <TextArea placeholder="请输入" maxLength={250} rows={2} showCount />
        </Form.Item>
        <Form.Item name="grade" label="活动分数">
          <Input placeholder="请输入" type="number" />
        </Form.Item>
        <Form.Item
          name="startTime"
          label="开始时间"
          onClick={async () => {
            setDatePickerVis(true);
            selectItem.current = 'startTime';
          }}
        >
          <Input readOnly placeholder="请选择开始时间" />
        </Form.Item>
        <Form.Item
          name="endTime"
          label="结束时间"
          onClick={async () => {
            setDatePickerVis(true);
            selectItem.current = 'endTime';
          }}
        >
          <Input readOnly placeholder="请选择结束时间" />
        </Form.Item>
        <Form.Item name="location" label="活动地点">
          <Input placeholder="请输入" />
        </Form.Item>
        <Form.Item name="organization" label="活动组织">
          <Input placeholder="请输入" />
        </Form.Item>
        <Form.Item name="initiator" label="活动发起人姓名">
          <Input placeholder="请输入" />
        </Form.Item>
        <Form.Item
          name="rankName"
          label="活动级别"
          onClick={async () => {
            setRankNameVis(true);
          }}
        >
          <Input placeholder="请选择" readOnly />
        </Form.Item>
        <Form.Item
          name="rankId"
          label="活动对象"
          onClick={async () => {
            if (from.getFieldValue('rankName') === '院级') return;
            setRankIdVis(true);
          }}
        >
          <Input placeholder="请输入" readOnly />
        </Form.Item>
        <Form.Item name="canNumber" label="可以参与人数">
          <Input placeholder="请输入" type="number" />
        </Form.Item>
        <Form.Item
          name="signWay"
          label="活动签到方式"
          onClick={async () => {
            setSignWayVis(true);
          }}
        >
          <Input placeholder="请选择" readOnly />
        </Form.Item>
      </Form>
      <DatePicker
        visible={datePickerVis}
        onClose={() => {
          setDatePickerVis(false);
        }}
        precision="minute"
        onConfirm={(val) => from.setFieldsValue({ [selectItem.current]: Dayjs(val).format('YYYY-MM-DD HH:mm:ss') })}
      />
      <Picker
        columns={[[{ label: '定位签到', value: '定位签到' }]]}
        visible={signWayVis}
        onClose={() => {
          setSignWayVis(false);
        }}
        onConfirm={(v) => {
          from.setFieldsValue({
            signWay: v[0],
          });
        }}
      />
      <Picker
        columns={[
          [
            { label: '院级', value: '院级' },
            { label: '班级', value: '班级' },
          ],
        ]}
        visible={rankNameVis}
        onClose={() => {
          setRankNameVis(false);
        }}
        onConfirm={(v) => {
          if (v[0] === '院级') {
            from.setFieldsValue({
              rankId: '分院全体成员',
            });
          }
          if (v[0] === '班级') {
            from.setFieldsValue({
              rankId: '',
            });
          }
          from.setFieldsValue({
            rankName: v[0],
          });
        }}
      />
      <Picker
        columns={[queryClassList.current?.map((item) => ({ label: item.className, value: item.className })) ?? []]}
        visible={rankIdVis}
        onClose={() => {
          setRankIdVis(false);
        }}
        onConfirm={(v) => {
          from.setFieldsValue({
            rankId: v[0],
          });
        }}
      />
    </div>
  );
}
