import { AxiosPromise } from 'axios';
import requestXhrHttp from '../utils/xhrHttp';

// axios.post('http://192.168.0.107:8686/upload/uploadSingleFile', formData, {
//   headers: {
//
//     Authorization: `Bearer ${window.localStorage.getItem('token') ?? ''}`,
//   },
// });
export function uploadSingleFile(data: FormData): AxiosPromise<{ uri: string }> {
  return requestXhrHttp({
    url: 'upload/uploadSingleFile',
    method: 'POST',
    headers: {
      'Content-Type': 'multipart/form-data',
    },
    data,
  });
}
