import { AxiosPromise } from 'axios';
import requestXhrHttp from '../../utils/xhrHttp';
import { queryClassType } from './data';

export function createActivity(data: any): AxiosPromise<{ uri: string }> {
  return requestXhrHttp({
    url: 'sutuo/createActivity',
    method: 'POST',
    data,
  });
}

export function queryClass(params: {
  pageNumber: number;
  pageSize: number;
}): AxiosPromise<{ list: queryClassType[]; total: number }> {
  return requestXhrHttp({
    url: 'class/queryClass',
    method: 'GET',
    params,
  });
}
